@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&subset=cyrillic);
@font-face {
  font-family: 'Bebas';
  src: url(/static/media/BebasNeueBook.90930772.eot);
  src: url(/static/media/BebasNeueBook.90930772.eot?#iefix) format('embedded-opentype'),
  url(/static/media/BebasNeueBook.8013005f.woff) format('woff'),
  url(/static/media/BebasNeueBook.176d7527.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
@media only screen and (max-width: 1450px) {
  html {
    font-size: 12px;
  }
}
@media only screen and (min-width: 1500px) {
html {
  font-size: 15px;
}
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  background: transparent;
  -webkit-transition: 0.3s ease color;
  transition: 0.3s ease color;
  font-size: 1.5em;
  font-weight: normal;
  font-family: 'Bebas';
  color: #F44336;
  border: none;
}

input {
  width: 100%;
  padding: 0.1em;
  color: #F44336;
  background: transparent;
  border: none;
  border-bottom: 2px solid #F44336;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  -webkit-transition-property: color, border;
  transition-property: color, border;
  font-family: 'Roboto', sans-serif;
  font-size: 1.5em;
  font-weight: 500;
}
input:focus {
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  -webkit-transition-property: color, border;
  transition-property: color, border;
  border-bottom: 2px solid white;
  outline: none;
  color: white;
}

textarea {
  width: 100%;
  padding: 0.1em;
  color: #F44336;
  background: transparent;
  border: none;
  border-bottom: 2px solid #F44336;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  -webkit-transition-property: color, border;
  transition-property: color, border;
  font-family: 'Roboto', sans-serif;
  font-size: 1.3em;
  font-weight: 500;
  resize: none;
}
textarea:focus {
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  -webkit-transition-property: color, border;
  transition-property: color, border;
  border-bottom: 2px solid white;
  outline: none;
  color: white;
}

h3 {
  font-size: 1.5em;
  font-family: 'Bebas';
  color: #F44336;
}

h1 {
  font-size: 3em;
  font-weight: 900;
  color: white;
  margin: 0;
}

h2 {
  font-size: 1.5em;
  opacity: 0.8;
  font-weight: 700;
  color: white;
}
p {
  font-size: 1em;
  opacity: 0.6;
  font-weight: 400;
  color: white;
}

